import React, { useEffect, useState } from "react";

const HeroSection: React.FC = () => {
  const [reverse, setReverse] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setReverse(window.scrollY > 0);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section
      id="hero"
      className="h-screen flex justify-end items-center bg-gradient-to-r from-blue-500 to-purple-600 text-white px-4 relative overflow-hidden"
    >
      <div
        className={`absolute bottom-10 whitespace-nowrap text-[12vw] font-extrabold tracking-widest animate-scroll ${reverse ? "reverse" : ""}`}
      >
        ANGHELO -- DEARROZ
      </div>

      <style>{`
        @keyframes scrollLeftToRight {
          0% { transform: translateX(-100%); }
          100% { transform: translateX(100%); }
        }

        @keyframes scrollRightToLeft {
          0% { transform: translateX(100%); }
          100% { transform: translateX(-100%); }
        }

        .animate-scroll {
          animation: scrollLeftToRight 12s linear infinite;
        }

        .animate-scroll.reverse {
          animation: scrollRightToLeft 12s linear infinite;
        }
      `}</style>
    </section>
  );
};

export default HeroSection;
